<template>
    <div class="content-bg" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">数据包类型</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="packetType2DateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">全局-数据包类型个数</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.allPacketsTypeData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "packetType2",
        data() {
            return {
              fullscreenLoading:false,
              allPacketsTypeData2:{
                baseConfig:{
                  title:'',
                  xAxisName:'类型',
                  yAxisName:'数据包/个数',
                  rotate:'20',
                  itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                },
                xAxisArr:['碎片包','正常包','特大包'],
                yAxisArr:[]

              },
            }
        },
        created(){
          /*监听刷新时间改变*/
          bus.$on('packetType2DateBus2',(arr)=>{
            //定义参数
            let paramObj = {
              starttime:arr[0],
              endtime:arr[1]
            }
            /*获取全局数据包类型个数*/
            this.fullscreenLoading = true;
            this.getAllPacketsTypeData(paramObj);
          })

        },
        mounted(){},
        methods:{
            /*获取全局数据包类型个数*/
            getAllPacketsTypeData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getPacketTypeCount.do',paramObj)
                        .then(res=>{
                          this.fullscreenLoading = false;
                          let arr = [];
                          arr.push(res[0].small.value[1]);
                          arr.push(res[0].normal.value[1]);
                          arr.push(res[0].big.value[1]);
                          this.allPacketsTypeData2.yAxisArr= arr;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
          vBasedate,
          vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



